@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

body {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 15px;
    color: white;
    background-color: black;
    align-items: center;
    margin: 0;
    padding: 0;

}

html {
    scroll-behavior: smooth;

}

body,
html {
    margin: 0;
    padding: 0;
    height: 100%;
}

@media (max-width: 768px) {
    body {
        font-size: 12px;

    }
}